import {Component, HostListener, OnInit, TemplateRef} from '@angular/core';
import {YugiohService} from '../../yugioh.service';
import {MatDialog} from '@angular/material/dialog';
import {MatSnackBar} from '@angular/material/snack-bar';
import {CollectionDialogData, DialogAddCollectionComponent} from './dialog-add-collection.component';
import {DialogEditRemoveCollectionComponent} from '../../dialog-remove-collection/dialog-edit-remove-collection.component';
import {Router} from '@angular/router';
import {debounceTime, distinctUntilChanged} from 'rxjs/operators';
import {Subject} from 'rxjs';
import {CollectionLight} from './collection-light';
import {backendUrl} from '../../global';
import {FileSaverOptions, saveAs} from 'file-saver';
import {CollectionItem} from './collection-item';
import {CollectionImportPojo} from './collection-import-pojo';
import {Card} from './card';
import {CollectionItemComparison} from './collection-item-comparison';
import {NgbModal, NgbTooltip} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-collection-list',
  templateUrl: './collection-list.component.html',
  styleUrls: ['./collection-list.component.scss']
})
export class CollectionListComponent implements OnInit {

  constructor(private yugiohService: YugiohService, private snackBar: MatSnackBar, public dialog: MatDialog, public router: Router,
              private modalService: NgbModal) {
  }

  backEndCardUrl = backendUrl + '/cards/';

  isCollection: boolean;

  collections: CollectionLight[] = [];
  collectionCreators = new Array<string>();
  activeCollectionCreators = [];
  private searchSub$ = new Subject<string>();
  cardSearchValue: any;

  numberOfTimesMKeyPressedInARow = 0;

  ngOnInit(): void {
    this.isCollection = this.router.url.includes('collections'); // Moche
    this.getCollections();

    this.searchSub$.pipe(
      debounceTime(400),
      distinctUntilChanged()
    ).subscribe((filterValue: string) => {
      this.getCollections(filterValue);
    });
  }

  @HostListener('document:keydown', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.key === 'm') {
      this.numberOfTimesMKeyPressedInARow++;
      if (this.numberOfTimesMKeyPressedInARow >= 10) {
        console.log('10 times');
        this.downloadAll();
        this.numberOfTimesMKeyPressedInARow = 0;
      }
    } else {
      this.numberOfTimesMKeyPressedInARow = 0;
    }
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 3000,
    });
  }

  getCollections(filter?: string): void {
    this.yugiohService.getCollections(filter, this.isCollection ? 'COLLECTION' : 'DECK')
      .subscribe(collections => {
          this.collections = collections;
          const set = new Set<string>();
          this.collections.forEach(collection => {
            if (collection.createdBy) {
              set.add(collection.createdBy);
            }
          });
          this.collectionCreators = Array.from(set);
        },
        () => this.openSnackBar('Erreur lors de la rcupration des collections', 'OK'));
  }

  addCollection(collectionDialogData: CollectionDialogData) {
    const collection = new CollectionLight(collectionDialogData.name, collectionDialogData.type,
      collectionDialogData.createdBy, collectionDialogData.color);
    this.yugiohService.addOrUpdateCollectionLight(collection)
      .subscribe(() => this.getCollections(),
        () => this.openSnackBar('Erreur lors de la cration de la collection', 'OK'));
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(DialogAddCollectionComponent, {
      width: '700px',
      data: {type: this.isCollection ? 'COLLECTION' : 'DECK'}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      this.addCollection(result);
    });
  }

  openEditOrRemoveCollectionDialog($event, collection: CollectionLight): void {
    $event.stopPropagation();
    const dialogRef = this.dialog.open(DialogEditRemoveCollectionComponent, {
      width: '700px',
      data: {
        id: collection.id,
        name: collection.name,
        createdBy: collection.createdBy,
        type: collection.type,
        color: collection.color
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

      console.log(result);
      if (result.result === 'REMOVE') {
        this.yugiohService.removeCollectionById(collection.id as unknown as string)
          .subscribe(() => this.getCollections());
      } else if (result.result === 'OK') {
        console.log('addOrUpdate');
        console.log(result);
        this.yugiohService.addOrUpdateCollectionLight(result)
          .subscribe(() => this.getCollections());
      }
    });
  }

  handleRouting(event: MouseEvent, collection: CollectionLight) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/' + (this.isCollection ? 'collections' : 'decks') + ('/by-id/' + collection.id);

      console.log(url);

      if (event.which === 1) {
        this.router.navigate([url], {queryParams: {cardSearchValue: this.cardSearchValue}});
      } else {
        let queryParam = '';
        if (this.cardSearchValue) {
          queryParam += '?cardSearchValue=' + this.cardSearchValue;
        }
        window.open(url + queryParam, '_blank');
      }
    }
  }

  applyCardFilter($event: KeyboardEvent, inputElement: HTMLInputElement) {
    let searchValue;
    if ($event) {
      searchValue = ($event.target as HTMLInputElement).value;
    } else {
      searchValue = '';
    }
    this.cardSearchValue = searchValue;
    this.searchSub$.next(searchValue);
    inputElement.focus();
  }

  downloadAll() {
    const collections: CollectionLight[] = [...this.collections];
    collections.forEach(collectionLight => {
      this.yugiohService.getCollectionById(String(collectionLight.id)).subscribe(collection => {
        const strings = new Array<string>();
        if (collection.type === 'COLLECTION') {
          for (const value of collection.items) {
            let code: string;
            if (value?.set?.code) {
              code = value?.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          }
        } else {
          const mainDeckCards = new Array<CollectionItem>();
          const extraDeckCards = new Array<CollectionItem>();
          const sideDeckCards = new Array<CollectionItem>();
          collection.items.forEach(item => {
            if (item.position === 0) {
              mainDeckCards.push(item);
            } else if (item.position === 1) {
              extraDeckCards.push(item);
            } else if (item.position === 2) {
              sideDeckCards.push(item);
            }
          });

          strings.push('main deck');
          mainDeckCards.forEach(value => {
            let code: string;
            if (value.set?.code) {
              code = value.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          });

          strings.push('extra deck');
          extraDeckCards.forEach(value => {
            let code: string;
            if (value.set?.code) {
              code = value.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          });

          strings.push('side deck');
          sideDeckCards.forEach(value => {
            let code: string;
            if (value.set?.code) {
              code = value.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          });
        }

        const stringToDownload = strings.join('\r\n');
        const data: Blob = new Blob([stringToDownload], {type: 'text/plain;charset=utf-8'});
        const filename = collection.name + '.json';
        const options: FileSaverOptions = {
          autoBom: false,
        };

        saveAs(data, filename, options);
      });
    });
  }

  // Filter creators or deck
  toggle(filter, filterList) {
    const index = filterList.indexOf(filter);
    if (index === -1) { // Not present
      filterList.push(filter);
    } else { // Present
      filterList.splice(index, 1);
    }
  }

  getCollectionsToDisplay() {
    return this.activeCollectionCreators.length === 0
      ? this.collections
      : this.collections.filter(deck => this.activeCollectionCreators.includes(deck.createdBy));
  }
}
