import {CollectionItem} from './collection-item';
import {Image} from './image';

export class Collection {
  id: number;
  name: string;
  type: string;
  createdBy: string;
  comment: string;
  items: Array<CollectionItem> = [];
  images: Array<Image> = [];
  color: string;
  mainCardApiId: string;

  totalItems: number; // Used only in front

  constructor(name: string, type: string) {
    this.name = name;
    this.type = type;
  }
}
