import {Component, Input, OnInit} from '@angular/core';
import {Card} from '../card';
import {backendUrl} from '../../../global';
import {YugiohService} from '../../../yugioh.service';

@Component({
  selector: 'app-tooltip-card',
  templateUrl: './tooltip-card.component.html',
  styleUrls: ['./tooltip-card.component.scss']
})
export class TooltipCardComponent implements OnInit {

  backEndCardUrl = backendUrl + '/cards/';

  @Input() card: Card;

  cardmarketPrice: string;

  constructor(private yugiohService: YugiohService) {
  }

  ngOnInit(): void {
    this.yugiohService.getCardPriceById(this.card?.apiId)
      .subscribe(value => {
        if (value?.data && value.data.length > 0) {
          if (value.data[0]?.card_prices && value.data[0].card_prices.length > 0) {
            this.cardmarketPrice = value.data[0].card_prices[0].cardmarket_price;
          }
        }
      });
  }

}
