import {Component, Inject} from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import {CardSort} from './card-sort';

@Component({
  selector: 'app-sort-dialog',
  templateUrl: './sort-dialog.component.html',
  styleUrls: ['./sort-dialog.component.scss']
})
export class SortDialogComponent {

  CardSort = CardSort;
  sort = CardSort.alphabeticalAsc;
  collectionView = true;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialogRef: MatDialogRef<SortDialogComponent>
  ) {
    this.sort = data.sort;
    this.collectionView = data.collectionView;
  }

  onCancelClick(): void {
    this.dialogRef.close(this.data.sort);
  }

  onConfirmClick(chosenFilter: CardSort): void {
    this.dialogRef.close(chosenFilter);
  }

}
