import {Component, OnInit} from '@angular/core';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {TranslateService} from '@ngx-translate/core';
import {YugiohService} from './yugioh.service';
import {Router} from '@angular/router';
import {idAllCollections} from './global';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    trigger('openClose', [
      state('open', style({
        transform: 'rotate(360deg)'
      })),
      state('closed', style({
        transform: 'rotate(-360deg)'
      })),
      transition('* => *', [
        animate('1s')
      ])
    ]),
    trigger('sideNavOpenClose', [
      state('open', style({
        transform: 'translateX(0)'
      })),
      state('closed', style({
        transform: 'translateX(-100%)'
      })),
      transition('* => *', [
        animate('0.5s')
      ])
    ])
  ]
})
export class AppComponent implements OnInit {
  title = 'angularDemo';
  isFrench = true;
  canScroll = true;
  isOpen = false;
  bodySize: number;
  scrollInProgress = false;
  englishCheck: boolean;

  deckNavItemActive: boolean;
  collectionNavItemActive: boolean;
  cardMatcherNavItemActive: boolean;
  cardConsultationItemActive: boolean;
  itemActive: number;

  constructor(public translate: TranslateService, public yugiohService: YugiohService, public router: Router) {
    translate.addLangs(['en', 'fr']);

    const browserLang = translate.getBrowserLang();
    translate.use(browserLang.match(/en|fr/) ? browserLang : 'en');

    // CardSet up the language switch
    this.englishCheck = translate.currentLang === 'en';
  }

  ngOnInit() {
    setTimeout(() => this.refreshActiveTab(this.router.url), 100);
  }

  refreshActiveTab(url) {
    if (url.includes('/decks')) {
      this.itemActive = 0;
    } else if (url.includes('/collection')) {
      this.itemActive = 1;
    } else if (url.includes('/card-matcher')) {
      this.itemActive = 2;
    } else if (url.includes('/cards')) {
      this.itemActive = 3;
    }
  }

  onAuxClick($event: MouseEvent, url: string) {
    $event.preventDefault();
    if ($event.which === 2) {
      window.open(url);
      this.refreshActiveTab(url);
    }
  }

  handleRoutingToDeckOrIndividualCollection(event: MouseEvent, isCollection?: boolean) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      console.log(isCollection);
      const url = '/yu-gi-oh/' + (isCollection ? 'collections' : 'decks');

      console.log(url);

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
      this.refreshActiveTab(url);
    }
  }

  handleRoutingToCollectionsByUser(event: MouseEvent) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = 'yu-gi-oh/collections/by-user';

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
      this.refreshActiveTab(url);
    }
  }

  handleRoutingToAllCollection(event: MouseEvent) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/collections/' + idAllCollections;

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
      this.refreshActiveTab(url);
    }
  }

  handleRoutingToCardMatcher(event: MouseEvent) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/card-matcher';

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
      this.refreshActiveTab(url);
    }
  }

  handleRoutingToCardConsultations(event: MouseEvent) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/cards';

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
      this.refreshActiveTab(url);
    }
  }

}
