export class CollectionImportPojo {
  cardNumber: number;
  cardName: string;
  cardCode: string;
  position: number;
  cardApiId: string; // Used in collection comparison


  constructor(cardNumber: number, cardName: string, cardCode: string, position: number, cardApiId?: string) {
    this.cardNumber = cardNumber;
    this.cardName = cardName;
    this.cardCode = cardCode;
    this.position = position;
    this.cardApiId = cardApiId;
  }
}
