import {CardPrice} from './card-price';
import {BanListInfo} from './ban-list-info';

export class Card {
  id: number;
  apiId: string;
  name: string;
  type: string;
  language: string;
  description: string;
  level: number;
  atk: number;
  def: number;
  race: string;
  archetype: string;
  attribute: string;
  setCodes: Array<string> = [];
  mapMainDeckQuantity: Map<string, number>;
  mapExtraDeckQuantity: Map<string, number>;
  mapSideDeckQuantity: Map<string, number>;
  // tslint:disable-next-line:variable-name
  card_prices: Array<CardPrice> = [];
  banListInfo: BanListInfo;


  price: number; // Price generated at runtime by querying ygopro api, not used in our backend
}
