import { CanDeactivate } from '@angular/router';
import {DeckComponent} from './yu-gi-oh/collection/deck.component';
import { Injectable } from "@angular/core";

@Injectable()
export class DeckDeactivateGuard implements CanDeactivate<DeckComponent> {

  canDeactivate(component: DeckComponent) {
    return component.canDeactivate();
  }
}
