import {Component, Inject} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {ColorEvent} from 'ngx-color';

export interface CollectionDialogData {
  name: string;
  type: string;
  createdBy: string;
  color: string;
}

@Component({
  selector: 'app-dialog-collection',
  templateUrl: 'dialog-add-collection.html',
})
export class DialogAddCollectionComponent {

  collectionTypes = ['DECK', 'COLLECTION'];
  data: CollectionDialogData = {
    name: '',
    type: 'DECK',
    createdBy: '',
    color: ''
  };

  constructor(
    public dialogRef: MatDialogRef<DialogAddCollectionComponent>,
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
  ) {
    this.data.type = dialogData.type;
  }

  changeComplete($event: ColorEvent) {
    this.data.color = $event.color.hex;
  }
}
