import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FilterDto} from '../filter-dialog/filter-dto';
import {FilterDialogComponent} from '../filter-dialog/filter-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import * as ydke from 'ydke';
import {CollectionImportPojo} from '../yu-gi-oh/collection/collection-import-pojo';
import {SortDialogComponent} from '../sort-dialog/sort-dialog.component';
import {CardSort} from '../sort-dialog/card-sort';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
  @Input() showYdke = true;
  @Input() showDownloadAndUpload = true;

  searchValue: string;
  @Output() searchValueChanged = new EventEmitter<string>();

  filterDto: FilterDto = new FilterDto();
  @Output() filterDtoChanged = new EventEmitter<FilterDto>();

  sort = CardSort.alphabeticalAsc;
  @Output() sortChanged = new EventEmitter<CardSort>();

  @Output() downloadClicked = new EventEmitter<void>();
  @Output() importYdkeClicked = new EventEmitter<Array<CollectionImportPojo>>();
  @Output() exportYdkeClicked = new EventEmitter<void>();
  @Output() importDialogClicked = new EventEmitter<void>();

  constructor(public dialog: MatDialog, private snackBar: MatSnackBar) {
  }

  ngOnInit(): void {
    // TODO : ajout parametrer searchValue by default (cas http://localhost:4200/yu-gi-oh/decks/24274?cardSearchValue=ultim)
    // TODO : afficher filtre icon avec border si filterDto != null
  }

  refreshSearchValue() {
    this.searchValueChanged.emit(this.searchValue);
  }

  isFiltered() {
    return this.filterDto.attributes.length > 0
      || this.filterDto.levels.length > 0
      || this.filterDto.cardTypes.length > 0
      || this.filterDto.monsterRaces.length > 0
      || this.filterDto.spellRaces.length > 0
      || this.filterDto.trapRaces.length > 0
      || this.filterDto.archetype;
  }

  openFilterDialog() {
    const dialog = this.dialog.open(FilterDialogComponent, {
      width: '500px',
      data: this.filterDto
    });

    dialog.afterClosed().subscribe(() => {
      this.filterDtoChanged.emit(this.filterDto);
    });
  }

  download() {
    this.downloadClicked.emit();
  }

  exportAsYdke() {
    this.exportYdkeClicked.emit();
  }

  // TODO : handle langue for import ydke (full french or full english)
  importAsYdke() {
    navigator.clipboard.readText().then(ydkeString => {
      try {
        const deck = ydke.parseURL(ydkeString);
        const lines = new Array<CollectionImportPojo>();

        const mapMainApiIdQuantity = new Map<number, number>();
        deck.main.forEach(apiId => {
          const quantity = mapMainApiIdQuantity.get(apiId);
          if (quantity) {
            mapMainApiIdQuantity.set(apiId, quantity + 1);
          } else {
            mapMainApiIdQuantity.set(apiId, 1);
          }
        });

        const mapExtraApiIdQuantity = new Map<number, number>();
        deck.extra.forEach(apiId => {
          const quantity = mapExtraApiIdQuantity.get(apiId);
          if (quantity) {
            mapExtraApiIdQuantity.set(apiId, quantity + 1);
          } else {
            mapExtraApiIdQuantity.set(apiId, 1);
          }
        });

        const mapSideApiIdQuantity = new Map<number, number>();
        deck.side.forEach(apiId => {
          const quantity = mapSideApiIdQuantity.get(apiId);
          if (quantity) {
            mapSideApiIdQuantity.set(apiId, quantity + 1);
          } else {
            mapSideApiIdQuantity.set(apiId, 1);
          }
        });

        mapMainApiIdQuantity.forEach((value, key) =>
          lines.push(new CollectionImportPojo(value, null, null, 0, String(key))));
        mapExtraApiIdQuantity.forEach((value, key) =>
          lines.push(new CollectionImportPojo(value, null, null, 1, String(key))));
        mapSideApiIdQuantity.forEach((value, key) =>
          lines.push(new CollectionImportPojo(value, null, null, 2, String(key))));

        console.log(lines);

        this.importYdkeClicked.emit(lines);
      } catch (exception) {
        this.openSnackBar('Deck impossible  importer : ' + exception, 'OK');
      }
    }, reason => this.openSnackBar('Deck impossible  importer : ' + reason, 'OK'));
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 3000,
    });
  }

  openImportDialog() {
    this.importDialogClicked.emit();
  }

  openSortDialog() {
    const dialogRef = this.dialog.open(SortDialogComponent, {
      width: '300px',
      data: {
        sort: this.sort !== undefined ? this.sort : null,
        collectionView: true
      }
    });

    dialogRef.afterClosed().subscribe(value => {
      this.sort = value;
      this.sortChanged.emit(this.sort);
    });
  }
}
