import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {YugiohService} from '../../yugioh.service';
import {MatSnackBar} from '@angular/material/snack-bar';

@Component({
  selector: 'app-collection-by-user',
  templateUrl: './collection-by-user.component.html',
  styleUrls: ['./collection-by-user.component.scss']
})
export class CollectionByUserComponent implements OnInit {

  collectionCreators = new Array<string>();

  constructor(public router: Router, private yugiohService: YugiohService, private snackBar: MatSnackBar) { }

  ngOnInit(): void {

    this.yugiohService.getCollections()
      .subscribe(collections => {
          const set = new Set<string>();
          collections.forEach(collection => {
            if (collection.createdBy) {
              set.add(collection.createdBy);
            }
          });
          this.collectionCreators = Array.from(set);
        },
        () => this.openSnackBar('Erreur lors de la rcupration des collections', 'OK'));
  }

  handleRouting(event: MouseEvent, creator: string) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/collections/by-user/' + creator;

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
    }
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 3000,
    });
  }

}
