import {Injectable} from '@angular/core';
import {HttpClient, HttpParams, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {backendUrl, httpOptions, ygoproApiUrl} from './global';
import {Card} from './yu-gi-oh/collection/card';
import {Card as OtherCard} from './yu-gi-oh/combo-graph-two/card';
import {Collection} from './yu-gi-oh/collection/collection';
import {Set} from './yu-gi-oh/collection/set';
import {CollectionImportPojo} from './yu-gi-oh/collection/collection-import-pojo';
import {CollectionImportOutputPojo} from './yu-gi-oh/collection/collection-import-output-pojo';
import {CollectionLight} from './yu-gi-oh/collection/collection-light';
import {CollectionItem} from './yu-gi-oh/collection/collection-item';
import {CollectionItemComparison} from './yu-gi-oh/collection/collection-item-comparison';
import {FilterDto} from './filter-dialog/filter-dto';
import {CardSort} from './sort-dialog/card-sort';

@Injectable({
  providedIn: 'root'
})
export class YugiohService {

  constructor(private http: HttpClient) {
  }

  getCardById(apiId: string): Observable<OtherCard> {
    return this.http.get<OtherCard>(backendUrl + '/cards/' + apiId, {headers: httpOptions.headers});
  }

  getCardPriceById(apiId: string): Observable<any> {
    const params = new HttpParams()
      .set('id', apiId);
    return this.http.get<any>(ygoproApiUrl, {params, headers: httpOptions.headers});
  }

  getCardsPriceById(apiIds: string[]): Observable<any> {
    console.log(apiIds);
    const params = new HttpParams()
      .set('id', apiIds.join(','));
    return this.http.get<any>(ygoproApiUrl, {params, headers: httpOptions.headers});
  }

  getCardsbyName(filter = ''): Observable<Array<Card>> {
    const params = new HttpParams()
      .set('filter', filter);
    return this.http.post<Array<Card>>(backendUrl + '/cards', {}, {params, headers: httpOptions.headers});
  }

  getCardsPaginated(pageIndex: number, pageSize: number, sort: CardSort, filter: string = '', filters?: FilterDto):
    Observable<HttpResponse<Array<Card>>> {
    const params = new HttpParams().set('pageSize', String(pageSize))
      .set('pageIndex', String(pageIndex)).set('filter', filter).set('sort', CardSort[sort]);
    return this.http.post<Array<Card>>(backendUrl + '/cards', filters, {params, headers: httpOptions.headers, observe: 'response'});
  }

  getCardbyName(name): Observable<Card> {
    return this.http.get<Card>(backendUrl + '/cards/name/' + name, {headers: httpOptions.headers});
  }

  findCardsInCollection(collectionsItems: CollectionItem[]): Observable<Array<Array<CollectionItemComparison>>> {
    return this.http.post<Array<Array<CollectionItemComparison>>>(backendUrl + '/findCardsInCollection',
      collectionsItems, {headers: httpOptions.headers});
  }

  getCardsByApiId(apidId: string): Observable<Array<Card>> {
    return this.http.get<Array<Card>>(backendUrl + '/cards/apiId/' + apidId, {headers: httpOptions.headers});
  }

  getCardsBySet(set: string): Observable<Array<Card>> {
    return this.http.get<Array<Card>>(backendUrl + '/cards/set/' + set, {headers: httpOptions.headers});
  }

  getSets(): Observable<Array<Set>> {
    return this.http.get<Array<Set>>(backendUrl + '/sets', {headers: httpOptions.headers});
  }

  getCollections(filter?: string, collectionType ?: string): Observable<Array<CollectionLight>> {
    let params = new HttpParams();
    if (filter) {
      params = params.set('filter', filter);
    }
    if (collectionType) {
      params = params.set('collectionType', collectionType);
    }

    console.log(collectionType);

    return this.http.get<Array<CollectionLight>>(backendUrl + '/collections', {params, headers: httpOptions.headers});
  }

  getCollectionsFull(): Observable<Array<Collection>> {
    return this.http.get<Array<Collection>>(backendUrl + '/collections/full', {headers: httpOptions.headers});
  }

  removeCollectionById(id: string): Observable<Collection> {
    return this.http.delete<Collection>(backendUrl + '/collections/' + id, {headers: httpOptions.headers});
  }

  getCollectionById(id: string): Observable<Collection> {
    return this.http.get<Collection>(backendUrl + '/collections/' + id, {headers: httpOptions.headers});
  }

  getCollectionsByCreator(createdBy: string): Observable<Collection[]> {
    return this.http.get<Collection[]>(backendUrl + '/collections/by-creator/' + createdBy, {headers: httpOptions.headers});
  }

  addOrUpdateCollectionLight(collection: CollectionLight): Observable<CollectionLight> {
    return this.http.post<CollectionLight>(backendUrl + '/collections/light', collection, {headers: httpOptions.headers});
  }

  addOrUpdateCollection(collection: Collection): Observable<Collection> {
    console.log(backendUrl + '/collections');
    return this.http.post<Collection>(backendUrl + '/collections', collection, {headers: httpOptions.headers});
  }

  importCollection(id: number, collectionPojos: CollectionImportPojo[]): Observable<CollectionImportOutputPojo> {
    return this.http.post<CollectionImportOutputPojo>(backendUrl + '/collections/' + id + '/import', collectionPojos,
      {headers: httpOptions.headers});
  }

  importCollectionYdke(id: number, collectionPojos: CollectionImportPojo[]): Observable<Collection> {
    return this.http.post<Collection>(backendUrl + '/collections/' + id + '/importYdke', collectionPojos,
      {headers: httpOptions.headers});
  }

  importCollectionBySet(id: number, setCode: string, language: string): Observable<Collection> {
    const params = new HttpParams().set('language', language);

    return this.http.post<Collection>(backendUrl + '/collections/' + id + '/import/' + setCode,
      {}, {params, headers: httpOptions.headers});
  }

  getArchetypes(): Observable<Array<string>> {
    return this.http.get<Array<string>>(backendUrl + '/archetypes', {headers: httpOptions.headers});
  }


  /*addCardWithSetToCollection(collectionItem: CollectionItem, add: boolean): Observable<CollectionItem> {
    const params = new HttpParams()
      .set('add', String(add));
    console.log(collectionItem);
    return this.http.post<CollectionItem>(backendUrl + '/collections', collectionItem, {'params': params, headers: httpOptions.headers});
  }*/
}
