import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {NotFoundComponent} from './not-found/not-found.component';
import {ComboGraphTwoComponent} from './yu-gi-oh/combo-graph-two/combo-graph-two.component';
import {CollectionListComponent} from './yu-gi-oh/collection/collection-list.component';
import {DeckComponent} from './yu-gi-oh/collection/deck.component';
import {DeckDeactivateGuard} from './deck-deactive-guard';
import {CollectionNewComponent} from './yu-gi-oh/collection/collection-new.component';
import {CollectionNewDeactivateGuard} from './collection-new-deactive-guard';
import {CollectionByUserComponent} from './yu-gi-oh/collection/collection-by-user.component';
import {CardMatcherComponent} from './yu-gi-oh/collection/card-matcher.component';
import {CardConsultationComponent} from './yu-gi-oh/collection/card-consultation.component';

const routes: Routes = [
  {path: '', component: CollectionListComponent},
  {path: 'yu-gi-oh/2', component: ComboGraphTwoComponent},
  {path: 'yu-gi-oh/collections', component: CollectionListComponent},
  {path: 'yu-gi-oh/decks', component: CollectionListComponent},
  {path: 'yu-gi-oh/decks/by-id/:id', component: DeckComponent, canDeactivate: [DeckDeactivateGuard]},
  {path: 'yu-gi-oh/collections/by-id/:id', component: CollectionNewComponent, canDeactivate: [CollectionNewDeactivateGuard]},
  {path: 'yu-gi-oh/collections/by-user', component: CollectionByUserComponent},
  {path: 'yu-gi-oh/collections/by-user/:name', component: CollectionNewComponent},
  {path: 'yu-gi-oh/collections/allCollections', component: CollectionNewComponent},
  {path: 'yu-gi-oh/card-matcher', component: CardMatcherComponent},
  {path: 'yu-gi-oh/cards', component: CardConsultationComponent},
  {path: 'not-found', component: NotFoundComponent},
  {path: '**', redirectTo: 'not-found'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
