import {AfterViewInit, Component, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {Card} from './card';
import {Utils} from './utils';
import {Subject} from 'rxjs';
import {FilterDto} from '../../filter-dialog/filter-dto';
import {CardSort} from '../../sort-dialog/card-sort';
import {debounceTime, distinctUntilChanged} from 'rxjs/operators';
import {MatPaginator, PageEvent} from '@angular/material/paginator';
import {SortDialogComponent} from '../../sort-dialog/sort-dialog.component';
import {FilterDialogComponent} from '../../filter-dialog/filter-dialog.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {YugiohService} from '../../yugioh.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {MatDialog} from '@angular/material/dialog';
import {Router} from '@angular/router';
import {backendUrl} from '../../global';

@Component({
  selector: 'app-card-consultation',
  templateUrl: './card-consultation.component.html',
  styleUrls: ['./card-consultation.component.scss']
})
export class CardConsultationComponent implements OnInit, AfterViewInit {

  @ViewChild('matPaginator') matPaginator: MatPaginator;

  backEndCardUrl = backendUrl + '/cards/';

  totalNumberOfCards = 0;
  defaultPageSize = 25;
  globalCards = new Array<Card>();
  mainDeckMap = Utils.cardColorMap;
  private searchSub2$ = new Subject<string>();
  cardSearchValue2: any;
  filterDto: FilterDto = new FilterDto();
  sort = CardSort.alphabeticalAsc;

  constructor(private yugiohService: YugiohService, private snackBar: MatSnackBar, public dialog: MatDialog, public router: Router,
              private modalService: NgbModal) {
  }

  ngOnInit(): void {
    this.searchSub2$.pipe(
      debounceTime(400),
      distinctUntilChanged()
    ).subscribe(() => {
      this.refreshPaginator();
    });
  }

  ngAfterViewInit(): void {
    this.refreshPaginator();
  }

  refreshPaginator() {
    this.matPaginator.pageIndex = 0;
    this.matPaginator._changePageSize(this.matPaginator.pageSize);
  }

  applyCardFilterForGlobalCards($event: KeyboardEvent, inputElement: HTMLInputElement) {
    let searchValue;
    if ($event) {
      searchValue = ($event.target as HTMLInputElement).value;
    } else {
      searchValue = '';
    }
    this.cardSearchValue2 = searchValue;
    this.searchSub2$.next(searchValue);
    inputElement.focus();
  }

  onPageChanged($event: PageEvent) {
    console.log('pageChanged');
    this.yugiohService.getCardsPaginated($event.pageIndex, $event.pageSize, this.sort, this.cardSearchValue2, this.filterDto)
      .subscribe(response => {
        this.globalCards = response.body;
        this.totalNumberOfCards = Number(response.headers.get('pageTotalElements'));
      });
  }

  openSortDialog() {
    const dialogRef = this.dialog.open(SortDialogComponent, {
      width: '300px',
      data: {
        sort: this.sort !== undefined ? this.sort : null,
        collectionView: false
      }
    });

    dialogRef.afterClosed().subscribe(value => {
      console.log(value);
      this.sort = value;
      this.refreshPaginator();
    });
  }

  filter() {
    this.filterDto.collectionView = false;
    const dialog = this.dialog.open(FilterDialogComponent, {
      width: '500px',
      data: this.filterDto
    });

    dialog.afterClosed().subscribe(() => {
      console.log(this.filterDto);
      this.refreshPaginator();
    });
  }

  isFiltered() {
    return this.filterDto.attributes.length > 0
      || this.filterDto.levels.length > 0
      || this.filterDto.cardTypes.length > 0
      || this.filterDto.monsterRaces.length > 0
      || this.filterDto.spellRaces.length > 0
      || this.filterDto.trapRaces.length > 0
      || this.filterDto.archetype;
  }

  open(content: TemplateRef<any>) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      // this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      // this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
}
