import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {YugiohService} from '../yugioh.service';
import {Set} from '../yu-gi-oh/collection/set';

export class ImportData {
  importText: string;
  setCode: string;
  language: string;
  selectedIndex: number;
}

@Component({
  selector: 'app-dialog-import-collection',
  templateUrl: './dialog-import-collection.component.html',
  styleUrls: ['./dialog-import-collection.component.scss']
})
export class DialogImportCollectionComponent implements OnInit {

  placeholder: string;
  collectionString: string;
  activeSet: Set;
  sets: Array<Set> = [];
  activeLanguage = 'fr';
  languages = ['fr', 'en'];
  selectedIndex = 0;

  constructor(
    public dialogRef: MatDialogRef<DialogImportCollectionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, yugiohService: YugiohService) {
    if (data?.type === 'DECK') {
      this.placeholder = `Main deck
1 Souleating Oviraptor SR04
1x Arosol de Destruction
Extra deck
1 Evolzar Dolkka
1 Superdreadnought Rail Cannon Gustav Max
Side deck
6x TrOU noIr
2 Ultimate Conductor Tyranno`;
    } else {
      this.placeholder = `1 Souleating Oviraptor SR04
1x Arosol de Destruction
6x TrOU noIr
2 Ultimate Conductor Tyranno`;
    }

    yugiohService.getSets().subscribe(value => {
      this.sets = value;
      this.activeSet = value[0];
    });

  }

  ngOnInit() {
  }

  onCancelClick(): void {
    this.dialogRef.close();
  }

  onConfirmClick(): void {
    const data = new ImportData();
    data.selectedIndex = this.selectedIndex;
    if (this.selectedIndex === 0) {
      data.importText = this.collectionString;
    } else if (this.selectedIndex === 1) {
      data.setCode = this.activeSet.code;
      data.language = this.activeLanguage;
    }
    this.dialogRef.close(data);
  }
}
