import {Card} from './card';
import {Position} from './position';
import {Set} from './set';

export class CollectionItem {
  id: number;
  position: Position;
  card: Card;
  set: Set;
  quantity = 1;
  owner: string; // Only used in front during aggregation of collections

  constructor(position: Position, card: Card, set: Set) {
    this.position = position;
    this.card = card;
    this.set = set;
  }
}
