// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

@NgModule({
    declarations: [
        AppComponent,
        SafePipe,
        ArraySortPipe,
        DragDropDirective,
        UploadFileComponent,
        EmptyLayoutComponent,
        LoadingDirective,
        LoadingComponent,
        SnackBarComponent,
        NotFoundComponent,
        ComboGraphTwoComponent,
        CollectionNewComponent,
        CollectionListComponent,
        DialogAddCollectionComponent,
        DialogEditRemoveCollectionComponent,
        DeckComponent,
        TooltipCardComponent,
        DialogImportCollectionComponent,
        FullImageDialogComponent,
        SortDialogComponent,
        BanPipe,
        FilterDialogComponent,
        YesNoDialogComponent,
        ModalCardDetailComponent,
        SearchComponent,
        DeckListComponent,
        CollectionByUserComponent,
        CardMatcherComponent,
        CardConsultationComponent,
        BarCollectionInformationComponent
    ],
  imports: [
    BrowserModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    AppRoutingModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    MatCardModule,
    MatFormFieldModule,
    NgbModule,
    MatButtonModule,
    MatSidenavModule,
    MatSlideToggleModule,
    MatRippleModule,
    MatIconModule,
    MatSnackBarModule,
    MatInputModule,
    MatSelectModule,
    MatDialogModule,
    MatCheckboxModule,
    MatTabsModule,
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatDividerModule,
    DragDropModule,
    MatAutocompleteModule,
    MatExpansionModule,
    DragulaModule.forRoot(),
    ColorSketchModule,
    ColorGithubModule,
    ColorCircleModule,
    // SocialLoginModule,
    MatProgressSpinnerModule,
    MatBadgeModule,
    MatTooltipModule,
  ],
    providers: [
        // {
        // provide: AuthServiceConfig,
        // useFactory: provideConfig
        // },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorNotifierInterceptor, multi: true },
        CollectionNewDeactivateGuard,
        DeckDeactivateGuard
    ],
    bootstrap: [AppComponent],
    exports: []
})
export class AppModule {
}
