import {TypeOfSummon} from './type-of-summon.enum';
import {v4 as uuidv4} from 'uuid';

export class CardNode {
  id: string;
  name: string;
  image: string;
  typeOfSummon: TypeOfSummon;
  children?: CardNode[] = [];
  brothers?: CardNode[] = []; // card needed beside a parent to create the child (ex: oviraptor would need a 4* card to XYZ summon a child)
  target: CardNode; // used to draw an arrow between a brother and a direct child from another cardNode
  fakeNode: boolean; // used to create a hidden div so a cardNode and its brother point to it and then the fake node points to their child/target


  constructor(name?: string, image?: string, typeOfSummon?: TypeOfSummon, target?: CardNode) {
    this.id = uuidv4();
    this.name = name;
    this.image = image;
    this.typeOfSummon = typeOfSummon;
    this.children = [];
    this.brothers = [];
    this.target = target;
  }
}
