import {Pipe} from '@angular/core';

@Pipe({
  name: 'sort'
})
export class ArraySortPipe {
  transform(array: Array<any>, field?: string, sortString?: boolean): Array<any> {
    if (!Array.isArray(array)) {
      return;
    }
    if (field) {
      if (sortString) {
        array.sort((a: string, b: string) => a[field].toLowerCase().localeCompare(b[field].toLowerCase()));
        return array;
      } else {
        array.sort((a: any, b: any) => {
          if (a[field] < b[field]) {
            return -1;
          } else if (a[field] > b[field]) {
            return 1;
          } else {
            return 0;
          }
        });
        return array;
      }
    } else {
      if (sortString) {
        array.sort((a: string, b: string) => a.toLowerCase().localeCompare(b.toLowerCase()));
        return array;
      } else {
        array.sort((a: any, b: any) => {
          if (a < b) {
            return -1;
          } else if (a > b) {
            return 1;
          } else {
            return 0;
          }
        });
        return array;
      }
    }
  }
}
