import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {ColorEvent} from 'ngx-color';
import { MatDialog } from '@angular/material/dialog';
import {YesNoDialogComponent} from '../yes-no-dialog/yes-no-dialog.component';

@Component({
  selector: 'app-dialog-remove-collection',
  templateUrl: './dialog-edit-remove-collection.component.html',
  styleUrls: ['./dialog-edit-remove-collection.component.scss']
})
export class DialogEditRemoveCollectionComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<DialogEditRemoveCollectionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, public dialog: MatDialog) {
  }

  ngOnInit() {
  }

  onCancelClick(): void {
    this.dialogRef.close();
  }

  onRemoveClick(): void {
    this.dialog.open(YesNoDialogComponent, {
      width: '400px',
    }).afterClosed().subscribe(value => {
      if (value) {
        this.data.result = 'REMOVE';
        this.dialogRef.close(this.data);
      }
    });
  }

  onConfirmClick(): void {
    this.data.result = 'OK';
    this.dialogRef.close(this.data);
  }

  changeComplete($event: ColorEvent) {
    this.data.color = $event.color.hex;
  }
}
