import {ActivatedRouteSnapshot, CanDeactivate, RouterStateSnapshot, UrlTree} from '@angular/router';
import {Observable} from 'rxjs';
import {CollectionNewComponent} from './yu-gi-oh/collection/collection-new.component';
import { Injectable } from "@angular/core";

@Injectable()
export class CollectionNewDeactivateGuard implements CanDeactivate<CollectionNewComponent> {
  canDeactivate(component: CollectionNewComponent, currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot,
                nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return component.canDeactivate();
  }


}
