export class CollectionLight {
  id: number;
  name: string;
  type: string;
  createdBy: string;
  color: string;
  mainCardApiId: string;

  // global data
  nbCards: number;

  // collection data
  nbMonsters: number;
  nbSpells: number;
  nbTraps: number;

  // deck data
  nbMainDeck: number;
  nbSideDeck: number;
  nbExtraDeck: number;


  constructor(name: string, type: string, createdBy: string, color: string) {
    this.name = name;
    this.type = type;
    this.createdBy = createdBy;
    this.color = color;
  }
}
