import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Collection} from '../yu-gi-oh/collection/collection';
import {backendUrl} from '../global';

@Component({
  selector: 'app-bar-collection-information',
  templateUrl: './bar-collection-information.component.html',
  styleUrls: ['./bar-collection-information.component.scss']
})
export class BarCollectionInformationComponent implements OnInit {

  backEndCardUrl = backendUrl + '/cards/';

  @Input() collection: Collection;
  @Input() staticCollection: boolean;
  @Input() canSave: boolean;
  @Input() totalPrice: number;

  @Output() save = new EventEmitter<void>();
  @Output() removeImage = new EventEmitter<void>();

  constructor() {
  }

  ngOnInit(): void {
  }

  saveCollection() {
    this.save.emit();
  }

  removeImageFromCollection() {
    this.removeImage.emit();
  }
}
