import {AfterViewInit, Component, HostListener, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {YugiohService} from '../../yugioh.service';
import {MatDialog} from '@angular/material/dialog';
import {MatPaginator, PageEvent} from '@angular/material/paginator';
import {MatSnackBar} from '@angular/material/snack-bar';
import {CollectionDialogData, DialogAddCollectionComponent} from './dialog-add-collection.component';
import {DialogEditRemoveCollectionComponent} from '../../dialog-remove-collection/dialog-edit-remove-collection.component';
import {Router} from '@angular/router';
import {debounceTime, distinctUntilChanged} from 'rxjs/operators';
import {Subject} from 'rxjs';
import {CollectionLight} from './collection-light';
import {backendUrl, idAllCollections} from '../../global';
import {FileSaverOptions, saveAs} from 'file-saver';
import {CollectionItem} from './collection-item';
import {CollectionImportPojo} from './collection-import-pojo';
import {Card} from './card';
import {CollectionItemComparison} from './collection-item-comparison';
import {NgbModal, NgbTooltip} from '@ng-bootstrap/ng-bootstrap';
import {Utils} from './utils';
import {FilterDialogComponent} from '../../filter-dialog/filter-dialog.component';
import {FilterDto} from '../../filter-dialog/filter-dto';
import {CardSort} from '../../sort-dialog/card-sort';
import {SortDialogComponent} from '../../sort-dialog/sort-dialog.component';

@Component({
  selector: 'app-deck-list',
  templateUrl: './deck-list.component.html',
  styleUrls: ['./deck-list.component.scss']
})
export class DeckListComponent implements OnInit, AfterViewInit {

  constructor(private yugiohService: YugiohService, private snackBar: MatSnackBar, public dialog: MatDialog, public router: Router,
              private modalService: NgbModal) {
  }

  @ViewChild('matPaginator') matPaginator: MatPaginator;

  backEndCardUrl = backendUrl + '/cards/';

  collections: CollectionLight[] = [];
  decks: CollectionLight[] = [];
  collectionCreators = new Array<string>();
  activeCollectionCreators = [];
  deckCreators = new Array<string>();
  activeDeckCreators = [];
  private searchSub$ = new Subject<string>();
  cardSearchValue: any;

  private searchSubDeck$ = new Subject<string>();
  cardSearchValueDeck: any;
  numberOfTimesMKeyPressedInARow = 0;
  placeholder = `1 Souleating Oviraptor
1x Arosol de Destruction
6x TrOU noIr
2 Ultimate Conductor Tyranno

Ne pas mettre de set, il faut forcment mettre un nombre avant le nom de la carte`;
  cardsForComparison = '';
  foundLines = new Array<CollectionImportPojo>();
  missingLines = new Array<CollectionImportPojo>();
  foundCollectionItems = new Array<CollectionItemComparison>();
  incompleteCollectionItems = new Array<CollectionItemComparison>();
  missingCollectionItems = new Array<CollectionItemComparison>();
  totalPrice: number;
  foundCollectionItemsPrice: number;
  incompleteCollectionItemsPrice: number;
  missingCollectionItemsPrice: number;

  // Cards consultation
  totalNumberOfCards = 0;
  defaultPageSize = 25;
  globalCards = new Array<Card>();
  mainDeckMap = Utils.cardColorMap;
  private searchSub2$ = new Subject<string>();
  cardSearchValue2: any;
  filterDto: FilterDto = new FilterDto();
  sort = CardSort.alphabeticalAsc;

  ngOnInit(): void {
    this.getCollections();

    this.searchSub$.pipe(
      debounceTime(400),
      distinctUntilChanged()
    ).subscribe((filterValue: string) => {
      this.getCollections(filterValue);
    });


    this.searchSubDeck$.pipe(
      debounceTime(400),
      distinctUntilChanged()
    ).subscribe((filterValue: string) => {
      this.getCollections(filterValue);
    });


    this.searchSub2$.pipe(
      debounceTime(400),
      distinctUntilChanged()
    ).subscribe(() => {
      this.refreshPaginator();
    });

  }

  ngAfterViewInit(): void {
    this.refreshPaginator();
  }

  @HostListener('document:keydown', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.key === 'm') {
      this.numberOfTimesMKeyPressedInARow++;
      if (this.numberOfTimesMKeyPressedInARow >= 10) {
        console.log('10 times');
        this.downloadAll();
        this.numberOfTimesMKeyPressedInARow = 0;
      }
    } else {
      this.numberOfTimesMKeyPressedInARow = 0;
    }
  }

  refreshPaginator() {
    this.matPaginator.pageIndex = 0;
    this.matPaginator._changePageSize(this.matPaginator.pageSize);
  }

  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 3000,
    });
  }

  getCollections(filter?: string): void {
    this.yugiohService.getCollections(filter)
      .subscribe(collections => {
          this.collections = collections.filter(collection => collection.type === 'COLLECTION');
          this.decks = collections.filter(collection => collection.type === 'DECK');
          const set = new Set<string>();
          this.collections.forEach(collection => {
            if (collection.createdBy) {
              set.add(collection.createdBy);
            }
          });
          this.collectionCreators = Array.from(set);

          const set2 = new Set<string>();
          this.decks.forEach(deck => {
            if (deck.createdBy) {
              set2.add(deck.createdBy);
            }
          });
          this.deckCreators = Array.from(set2);
        },
        () => this.openSnackBar('Erreur lors de la rcupration des collections', 'OK'));
  }

  addCollection(collectionDialogData: CollectionDialogData) {
    const collection = new CollectionLight(collectionDialogData.name, collectionDialogData.type,
      collectionDialogData.createdBy, collectionDialogData.color);
    this.yugiohService.addOrUpdateCollectionLight(collection)
      .subscribe(() => this.getCollections(),
        () => this.openSnackBar('Erreur lors de la cration de la collection', 'OK'));
  }

  openDialog(type: string): void {
    const dialogRef = this.dialog.open(DialogAddCollectionComponent, {
      width: '700px',
      data: {type}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      this.addCollection(result);
    });
  }

  openEditOrRemoveCollectionDialog($event, collection: CollectionLight): void {
    $event.stopPropagation();
    const dialogRef = this.dialog.open(DialogEditRemoveCollectionComponent, {
      width: '700px',
      data: {
        id: collection.id,
        name: collection.name,
        createdBy: collection.createdBy,
        type: collection.type,
        color: collection.color
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

      console.log(result);
      if (result.result === 'REMOVE') {
        this.yugiohService.removeCollectionById(collection.id as unknown as string)
          .subscribe(() => this.getCollections());
      } else if (result.result === 'OK') {
        console.log('addOrUpdate');
        console.log(result);
        this.yugiohService.addOrUpdateCollectionLight(result)
          .subscribe(() => this.getCollections());
      }
    });
  }

  handleRouting(event: MouseEvent, collection: CollectionLight) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      let collectionType: string;
      if (collection.type === 'DECK') {
        collectionType = 'decks';
      } else if (collection.type === 'COLLECTION') {
        collectionType = 'collections';
      }
      const url = '/yu-gi-oh/' + collectionType + '/' + collection.id;

      if (event.which === 1) {
        this.router.navigate([url], {queryParams: {cardSearchValue: this.cardSearchValue}});
      } else {
        let queryParam = '';
        if (this.cardSearchValue) {
          queryParam += '?cardSearchValue=' + this.cardSearchValue;
        }
        window.open(url + queryParam, '_blank');
      }
    }
  }

  handleRouting2(event: MouseEvent, creator: string) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/collections/' + creator;

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
    }
  }

  handleRouting3(event: MouseEvent) {
    event.preventDefault();
    if (event.which === 2 || event.which === 1) {
      const url = '/yu-gi-oh/collections/' + idAllCollections;

      if (event.which === 1) {
        this.router.navigate([url]);
      } else {
        window.open(url, '_blank');
      }
    }
  }

  applyCardFilterForDecks($event: KeyboardEvent, inputElement: HTMLInputElement) {
    let searchValue;
    if ($event) {
      searchValue = ($event.target as HTMLInputElement).value;
    } else {
      searchValue = '';
    }
    this.cardSearchValueDeck = searchValue;
    this.searchSubDeck$.next(searchValue);
    inputElement.focus();
  }

  applyCardFilter($event: KeyboardEvent, inputElement: HTMLInputElement) {
    let searchValue;
    if ($event) {
      searchValue = ($event.target as HTMLInputElement).value;
    } else {
      searchValue = '';
    }
    this.cardSearchValue = searchValue;
    this.searchSub$.next(searchValue);
    inputElement.focus();
  }

  applyCardFilterForGlobalCards($event: KeyboardEvent, inputElement: HTMLInputElement) {
    let searchValue;
    if ($event) {
      searchValue = ($event.target as HTMLInputElement).value;
    } else {
      searchValue = '';
    }
    this.cardSearchValue2 = searchValue;
    this.searchSub2$.next(searchValue);
    inputElement.focus();
  }

  downloadAll() {
    const collectionsAndDecks: CollectionLight[] = [...this.collections, ...this.decks];
    collectionsAndDecks.forEach(collectionLight => {
      this.yugiohService.getCollectionById(String(collectionLight.id)).subscribe(collection => {
        const strings = new Array<string>();
        if (collection.type === 'COLLECTION') {
          for (const value of collection.items) {
            let code: string;
            if (value?.set?.code) {
              code = value?.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          }
        } else {
          const mainDeckCards = new Array<CollectionItem>();
          const extraDeckCards = new Array<CollectionItem>();
          const sideDeckCards = new Array<CollectionItem>();
          collection.items.forEach(item => {
            if (item.position === 0) {
              mainDeckCards.push(item);
            } else if (item.position === 1) {
              extraDeckCards.push(item);
            } else if (item.position === 2) {
              sideDeckCards.push(item);
            }
          });

          strings.push('main deck');
          mainDeckCards.forEach(value => {
            let code: string;
            if (value.set?.code) {
              code = value.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          });

          strings.push('extra deck');
          extraDeckCards.forEach(value => {
            let code: string;
            if (value.set?.code) {
              code = value.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          });

          strings.push('side deck');
          sideDeckCards.forEach(value => {
            let code: string;
            if (value.set?.code) {
              code = value.set?.code;
            } else {
              code = '';
            }
            strings.push(value.quantity + ' ' + value.card.name + ' ' + code);
          });
        }

        const stringToDownload = strings.join('\r\n');
        const data: Blob = new Blob([stringToDownload], {type: 'text/plain;charset=utf-8'});
        const filename = collection.name + '.json';
        const options: FileSaverOptions = {
          autoBom: false,
        };

        saveAs(data, filename, options);
      });
    });
  }

  getCardsForComparison() {
    this.totalPrice = 0;
    const cardLines = this.cardsForComparison.split(/[\r\n]+/);
    console.log(cardLines);

    const lines = new Array<CollectionImportPojo>();

    for (const line of cardLines) {
      if (line && line.trim().length > 0) {
        if (line.charAt(0) >= '0' && line.charAt(0) <= '9') { // first character is a number
          const lineSplit = line.trim().split(/ +/); // to handle cases like "  first    middle    last    "
          // tslint:disable-next-line:max-line-length
          const cardNumber = lineSplit[0].split('x')[0];
          let cardName = '';
          // tslint:disable-next-line:prefer-for-of
          for (let i = 1; i < lineSplit.length; i++) {
            if (lineSplit[i] && lineSplit.length > 0) {
              cardName += lineSplit[i] + ' ';
            }
          }
          lines.push(new CollectionImportPojo(Number(cardNumber), cardName.trim(), null, 0));
        }
      }
    }

    console.log(lines);
    this.foundCollectionItems = undefined;
    this.incompleteCollectionItems = undefined;
    this.missingCollectionItems = undefined;
    this.foundLines = [];
    this.missingLines = [];
    let nbLinesGot = lines.length;
    // TODO : JS pipe or something like that
    for (const line of lines) {
      this.yugiohService.getCardbyName(line.cardName).subscribe(value => {
        line.cardApiId = value.apiId;
        this.foundLines.push(line);
        console.log(this.foundLines);
        nbLinesGot -= 1;
        if (nbLinesGot === 0) {
          this.compareToCollections();
        }
      }, error => {
        this.missingLines.push(line);
        nbLinesGot -= 1;
        if (nbLinesGot === 0) {
          this.compareToCollections();
        }
      });
    }
  }

  compareToCollections() {
    const collectionItems: CollectionItem[] = [];

    this.foundLines.forEach(line => {
      const card = new Card();
      card.name = line.cardName;
      card.apiId = line.cardApiId;
      const collectionItem = new CollectionItem(0, card, null);
      collectionItem.quantity = line.cardNumber;
      collectionItems.push(collectionItem);
    });

    this.generateTotalPrice(collectionItems);
    this.findCardsInCollectionAndAssociatePrices(collectionItems);
  }

  private generateTotalPrice(collectionItems: CollectionItem[]) {
    const apiIds = collectionItems.map(value => value.card.apiId);
    this.yugiohService.getCardsPriceById(apiIds)
      .subscribe(value => {
        console.log(value);
        if (value?.data && value.data.length > 0) {
          const map = new Map<string, number>();
          for (const data of value.data) {
            map.set(String(data.id), data.card_prices[0].cardmarket_price);
          }
          this.totalPrice = 0;
          console.log(map);
          collectionItems.forEach(item => {
            const price = Number(map.get(item.card.apiId));
            if (price) {
              this.totalPrice += price * item.quantity;
              item.card.price = price;
            } else {
              console.log(item.card.apiId);
              item.card.price = 0;
            }
          });
          console.log(this.totalPrice);
          // Keep only last 2 digits
          this.totalPrice = Number(this.totalPrice.toFixed(2));
          this.findCardsInCollectionAndAssociatePrices(collectionItems);
        }
      });
  }

  private findCardsInCollectionAndAssociatePrices(collectionItems: CollectionItem[]) {
    this.foundCollectionItemsPrice = 0;
    this.incompleteCollectionItemsPrice = 0;
    this.missingCollectionItemsPrice = 0;
    this.yugiohService.findCardsInCollection(collectionItems)
      .subscribe(value => {
        console.log(value);
        value[0].forEach(item => {
          item.price = collectionItems.filter(itemm => itemm.card.name === item.cardName)[0]?.card?.price;
          this.foundCollectionItemsPrice += item.price;
        });
        value[1].forEach(item => {
          item.price = collectionItems.filter(itemm => itemm.card.name === item.cardName)[0]?.card?.price;
          this.incompleteCollectionItemsPrice += item.price;
        });
        value[2].forEach(item => {
          item.price = collectionItems.filter(itemm => itemm.card.name === item.cardName)[0]?.card?.price;
          this.missingCollectionItemsPrice += item.price;
        });

        // Keep only last 2 digits
        this.foundCollectionItemsPrice = Number(this.foundCollectionItemsPrice.toFixed(2));
        this.incompleteCollectionItemsPrice = Number(this.incompleteCollectionItemsPrice.toFixed(2));
        this.missingCollectionItemsPrice = Number(this.missingCollectionItemsPrice.toFixed(2));

        this.foundCollectionItems = value[0];
        this.incompleteCollectionItems = value[1];
        this.missingCollectionItems = value[2];
      });
  }

  onPageChanged($event: PageEvent) {
    console.log('pageChanged');
    this.yugiohService.getCardsPaginated($event.pageIndex, $event.pageSize, this.sort, this.cardSearchValue2, this.filterDto)
      .subscribe(response => {
        this.globalCards = response.body;
        this.totalNumberOfCards = Number(response.headers.get('pageTotalElements'));
      });
  }

  open(content: TemplateRef<any>, ngbTooltip: NgbTooltip) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      // this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      // this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });

    for (let i = 0; i < 11; i++) {
      setTimeout(() => ngbTooltip.close(), 100 * i);
    }
  }

  openSortDialog() {
    const dialogRef = this.dialog.open(SortDialogComponent, {
      width: '300px',
      data: {
        sort: this.sort !== undefined ? this.sort : null,
        collectionView: false
      }
    });

    dialogRef.afterClosed().subscribe(value => {
      console.log(value);
      this.sort = value;
      this.refreshPaginator();
    });
  }

  filter() {
    this.filterDto.collectionView = false;
    const dialog = this.dialog.open(FilterDialogComponent, {
      width: '500px',
      data: this.filterDto
    });

    dialog.afterClosed().subscribe(() => {
      console.log(this.filterDto);
      this.refreshPaginator();
    });
  }

  isFiltered() {
    return this.filterDto.attributes.length > 0
      || this.filterDto.levels.length > 0
      || this.filterDto.cardTypes.length > 0
      || this.filterDto.monsterRaces.length > 0
      || this.filterDto.spellRaces.length > 0
      || this.filterDto.trapRaces.length > 0
      || this.filterDto.archetype;
  }

  // Filter creators or deck
  toggle(filter, filterList) {
    const index = filterList.indexOf(filter);
    if (index === -1) { // Not present
      filterList.push(filter);
    } else { // Present
      filterList.splice(index, 1);
    }
  }

  getDecksToDisplay() {
    return this.activeDeckCreators.length === 0
      ? this.decks
      : this.decks.filter(deck => this.activeDeckCreators.includes(deck.createdBy));
  }

  getCollectionsToDisplay() {
    return this.activeCollectionCreators.length === 0
      ? this.collections
      : this.collections.filter(deck => this.activeCollectionCreators.includes(deck.createdBy));
  }
}
