import {Injectable} from '@angular/core';
import {HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {MatSnackBar} from '@angular/material/snack-bar';
import {tap} from 'rxjs/operators';
import {SnackBarComponent, SnackBarData} from './snack-bar/snack-bar.component';
import {Router} from '@angular/router';
import {TranslateService} from '@ngx-translate/core';

@Injectable()
export class ErrorNotifierInterceptor implements HttpInterceptor {

  constructor(private snackBar: MatSnackBar, private router: Router, private translate: TranslateService) {
  }

  intercept(req: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    return next.handle(req).pipe(
      tap((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
          // console.log(event);
          const splitUrl = req.url.split('/');
          const endpoint = splitUrl[splitUrl.length - 1];
          // A 2XX status code is contained in the event and the event is not a GET, or login or logout
          if (req.method.localeCompare('GET') !== 0
            && endpoint.localeCompare('login') !== 0
            && endpoint.localeCompare('logout') !== 0
            && Math.floor(event.status / 100) === 2
            && !req.url.includes('/cards')) {

            const params: SnackBarData = {
              isSuccess: true,
              body: req.body,
              endpoint,
              url: req.url,
              extraMessage: null,
              doAction: () => this.snackBar.dismiss()
            };

            this.snackBar.openFromComponent(SnackBarComponent,
              {
                data: params,
                duration: 5000
              });
          }
        }
      }, (error: HttpEvent<any>) => {
        if (error instanceof HttpErrorResponse) {
          const splitUrl = req.url.split('/');
          if (error.status === 404) {
            this.router.navigateByUrl('/not-found', {replaceUrl: true});
          } else if (error.status === 401) {

            this.translate.get('UNAUTHORIZED').subscribe(value => {
              const params: SnackBarData = {
                isSuccess: false,
                body: req.body,
                endpoint: splitUrl[splitUrl.length - 1],
                url: req.url,
                extraMessage: value,
                doAction: () => {
                  // retry
                  console.log('ok');
                  this.snackBar.dismiss();
                  /*return next.handle(req);*/ // TODO : find how to do the retry
                }
              };

              this.snackBar.openFromComponent(SnackBarComponent,
                {
                  data: params,
                  duration: 200000
                });
            });
          } else if (Math.floor(error.status / 100) >= 4) { // 4XX or 5XX status code (except 404 because it's processed before)
            const params: SnackBarData = {
              isSuccess: false,
              body: req.body,
              endpoint: splitUrl[splitUrl.length - 1],
              url: req.url,
              extraMessage: null,
              doAction: () => {
                // retry
                console.log('ok');
                this.snackBar.dismiss();
                /*return next.handle(req);*/ // TODO : find how to do the retry
              }
            };

            this.snackBar.openFromComponent(SnackBarComponent,
              {
                data: params,
                duration: 200000
              });
          }
        }
      })
    );
  }
}
