import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'ban'
})
export class BanPipe implements PipeTransform {

  transform(value: string, ...args: any[]): string {
    return String(value === 'Banned' ? 0 : value === 'Limited' ? 1 : value === 'Semi-Limited' ? 2 : 3);
  }

}
